<template>
  <div class="register-container">
    <h1></h1>
    <el-form :model="registerForm" :rules="rules" ref="registerFormRef" label-width="120px" class="register-form">
      <el-form-item label="" prop="username">
        <el-input v-model="registerForm.username" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="registerForm.password" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="registerForm.phone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-radio-group v-model="registerForm.sex">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="idNumber">
        <el-input v-model="registerForm.idNumber" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="registerForm.email" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="image">
        <el-upload
          class="avatar-uploader"
          action="http://localhost:9999/common/upload"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <img v-if="registerForm.image" :src="registerForm.image + '?' + new Date().getTime()" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item :rules="[...(rules.image || []), { required: true, message: '', trigger: 'blur' }]">
        <!--  -->
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue';
import { ElMessage } from 'element-plus';
import axios from 'axios';


// 
const registerForm = reactive({
  username: '',
  image: '',
  password: '',
  phone: '',
  sex: '',
  email: '',
  idNumber: ''
});

// 
const rules = {
  username: [
    { required: true, message: '', trigger: 'blur' }
  ],
  password: [
    { required: true, message: '', trigger: 'blur' }
  ],
  phone: [
    { required: true, message: '', trigger: 'blur' }
  ],
  sex: [
    { required: true, message: '', trigger: 'change' }
  ],
  idNumber: [
    { required: true, message: '', trigger: 'blur' }
  ],
  email: [
    { required: true, message: '', trigger: 'blur' },
    { type: 'email', message: '', trigger: ['blur', 'change'] }
  ],
  image: [] 
};

// 
const registerFormRef = ref(null);

// 
const handleAvatarSuccess = (response, file) => {
  if (response.code === 200 && response.data) {
    //  data  URL
    registerForm.image = response.data;
    ElMessage.success('');
  } else {
    ElMessage.error('');
  }
};

// 
const beforeAvatarUpload = (file) => {
  const isImage = file.type.indexOf('image') === 0;
  if (!isImage) {
    ElMessage.error('');
    return false;
  }
  return true;
};

// 
const submitForm = () => {
  registerFormRef.value.validate((valid) => {
    if (valid) {
      // 
      let sexValue;
      if (registerForm.sex === '') {
        sexValue = 1;
      } else if (registerForm.sex === '') {
        sexValue = 0;
      }

      //  registerForm  sex 
      const formData = {
       ...registerForm,
        sex: sexValue
      };

      axios.post('http://localhost:9999/user/register', formData)
       .then(response => {
          if (response.status === 200) {
            ElMessage.success('');
            router.push({ name: 'login' });
          } else {
            ElMessage.error('');
          }
        })
       .catch(error => {
          ElMessage.error('' + error.message);
        });
    } else {
      ElMessage.error('');
      return false;
    }
  });
};
</script>

<style scoped>
.register-container {
  width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.register-form {
  margin-top: 20px;
}

.avatar-uploader {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
}

.avatar {
  width: 100px;
  height: 100px;
  display: block;
  object-fit: cover;
}
</style>